import React, {Component} from "react";

export class OurPets extends Component {
    render(){
        let rows = null;
        if (this.props.animals != null) {
            rows = this.props.animals.map((p, i) => {
                let imgsrc = null;
                if (p.photos != null && p.photos.length > 0 && p.photos[0].medium != null) {
                    imgsrc = <img style={{width: "100%"}} src={p.photos[0].medium} alt="" />;
                }
                return (
                    <div className="col-xs-12 col-sm-6 col-xl-4" key={i}>
                        <a href={p.url} target="blank">
                            <div className="petname">{p.name}</div>
                            <div>{imgsrc}</div>
                        </a>
                    </div>
                );
            });
        }

        return (
            <div className="row">
                {rows}
            </div>
        );
    }

    xrender() {
        let rows = null;
        if (this.props.animals != null) {
          rows = this.props.animals.map((p, i) => {
            let imgsrc = null;
            if (
              p.photos != null &&
              p.photos.length > 0 &&
              p.photos[0].medium != null
            ) {
              imgsrc = <img src={p.photos[0].medium} alt="" />;
            }
            return (
              <tr key={i}>
                <td>
                  <div className="petname">{p.name}</div>
                  <div>{imgsrc}</div>
                </td>
                <td>
                  <div>{p.age}</div>
                  <div>{p.gender}</div>
                  <div>{p.size}</div>
                  <div>{p.coat}</div>
                </td>
                <td>
                  <div>{p.description}</div>
                </td>
              </tr>
            );
          });
        }

        return (
          <div className="ourpets">
            <table>
              <tbody>{rows}</tbody>
            </table>
          </div>
        );
    }
}

