//import React, {useCallback} from "react";
import React from "react";
import { Link } from "react-router-dom";

export const ToolbarButton = ({ imageSrc, text, buttonClick, linkRef, newTab, children }) => {
    /*
    const handleClick = useCallback(() => {
        buttonClick();
    });
    */
   let img = null;
   if(imageSrc!=null){
       img = <img alt="" src={imageSrc} />
   }

    let target = null;
    if(newTab==="true"){
        target = (
          <a href={linkRef} target="blank" className="middle">
            <div>
                {img}
                {children}
            </div>
            <div className="text">{text}</div>
          </a>
        );
    }
    else{
        target = (
          <Link to={linkRef}>
            <div>
                {img}
                {children}
            </div>
            <div className="text">{text}</div>
          </Link>
        );

    }

    return (
        <li className="nav-item" >
            <div className="menu-button" data-toggle="collapse"
                data-target="#navbarCollapse">
                <div className="menu-button-content">{target}</div>
            </div>
        </li>

    );
}

